import Loading from '../../public/Loading'
import Head from '../../public/head'
export default {
  name: "playerReturn",
  data(){
    return{
      api: this.GLOBAL.baseURL,
      index:1,
      options:[
        {label:'全退',value:1},
      ],
      value:1,
      reson:'',
      houseList:[],
      house:'',
      order_id:"",
      isLoading:false
    }
  },
  components:{
    Head,
    Loading
  },
  methods:{
    goBack(){
      this.$router.go(-1)
    },
    //提交申请
    sendRefund(){
      this.isLoading = true
      if(this.house.is_refund){
        let a = []
        for(let i =0;i< this.house.house.length;i++){
          a.push({oh_id:this.house.house[i].oh_id,num:this.house.house[i].num - this.house.house[i].refund_num})
        }
        this.$http.post(this.api + '/RefundS',{
          token: localStorage.getItem('token'),
          order_id: this.house.order_id,
          person_num: this.house.num - this.house.refund_num,
          house: JSON.stringify(a),
          reason: this.reson,
          flag: 0,
          type: 1
        })
          .then(res=>{
            if(res.data.code == 1){
              this.index = 2
              this.isLoading = false
            }else if(res.data.code == 3){
              this.$http.post(this.api + '/home/index/token')
                .then(res=>{
                  localStorage.setItem('token',res.data.data)
                  this.sendRefund()
                })
            }else if(res.data.code == 0){
              alert(res.data.msg)
              this.isLoading = false
            }
          })
      }else{
        this.$http.post(this.api + '/RefundS',{
          token: localStorage.getItem('token'),
          order_id: this.house.order_id,
          reason: this.reson,
          flag: 1,
          type: 1
        })
          .then(res=>{
            if(res.data.code == 1){
              this.index = 2
              this.isLoading = false
            }else if(res.data.code == 3){
              this.$http.post(this.api + '/home/index/token')
                .then(res=>{
                  localStorage.setItem('token',res.data.data)
                  this.sendRefund()
                })
            }else if(res.data.code == 0){
              alert(res.data.msg)
              this.isLoading = false
            }
          })
      }
    },
    getOrder(){
      this.isLoading = true
      this.$http.post(this.api + '/OrderD',{
        token: localStorage.getItem('token'),
        order_id: this.order_id,
      })
        .then(res=>{
          if(res.data.code == 1){
            this.house = res.data.data
            this.isLoading = false
          }else if(res.data.code == 3){
            this.$http.post(this.api + '/home/index/token')
              .then(res=>{
                localStorage.setItem('token',res.data.data)
                this.getOrder()
              })
          }else if(res.data.code == 0){
            alert(res.data.msg)
            this.isLoading =false
          }
        })
    },
  },
  mounted(){
    this.getOrder()
  },
  created(){
    this.order_id = this.$route.query.information
  }
}



